import { registerBlockType } from "@quillforms/blocks";
import display from "./display";

registerBlockType("number-choice", {
  supports: {
    editable: true
  },
  attributes: {
    start: {
      type: "number",
      default: 0
    },
    end: {
      type: "number",
      default: 5
    }
  },
  display
});
