import { useEffect, useState } from "react";
import { useMessages } from "@quillforms/renderer-core";
import { css } from "emotion";
import classnames from "classnames";

const PhoneNumberDisplay = (props) => {
  const { attributes, setIsValid, setValidationErr, setVal } = props;

  const { required, placeholder } = attributes;
  const [number, setNumber] = useState("");
  const messages = useMessages();

  useEffect(() => {
    checkfieldValidation();
    // eslint-disable-next-line
  }, [number]);

  const checkfieldValidation = () => {
    if (required === true && (!number || number === "")) {
      setIsValid(false);
      setValidationErr(messages["label.errorAlert.required"]);
      setVal("");
    } else {
      setIsValid(true);
      setValidationErr(null);
      setVal(number);
    }
  };

  return (
    <>
      <div
        className={css`
          display: flex;
          flex-wrap: wrap;
          width: 100%;
          margin-top: 32px;
        `}
      >
        <input
          type="text"
          value={number}
          placeholder={placeholder}
          className={classnames(
            css`
              width: 100%;
              font-size: 30px;
              line-height: 30px;
              font-family: Roboto;
              color: rgb(255, 255, 255);
              background: transparent;
              background-color: transparent;
              border: none !important;
              outline: none;
              padding-bottom: 8px;
              border-radius: 0 !important;
              background: transparent;
              background-color: transparent !important;
              -webkit-transition: box-shadow 0.1s ease-out 0s;
              transition: box-shadow 0.1s ease-out 0s;
              box-shadow: rgba(255, 255, 255, 0.3) 0px 1px !important;
              -moz-appearance: textfield;
              :focus {
                box-shadow: rgb(255, 255, 255) 0px 2px !important;
                border: none !important;
                outline: none !important;
              }
              ::placeholder {
                opacity: 0.5;
                color: #ddd;
              }
            `
          )}
          // eslint-disable-next-line
          onChange={(e) => setNumber(e.target.value.replace(/[^0-9\+]/g, ""))}
        />
      </div>
    </>
  );
};

export default PhoneNumberDisplay;
